"use client"

import type React from "react"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { motion } from "framer-motion"
import { storage } from "@/lib/storage"
import type { AuthUser } from "@/lib/types"
import Link from "next/link"
import { ChevronLeft } from "lucide-react"

export default function SettingsPage() {
  const router = useRouter()
  const [auth, setAuth] = useState<AuthUser | null>(null)
  const [businessName, setBusinessName] = useState("")
  const [phone, setPhone] = useState("")
  const [address, setAddress] = useState("")
  const [online, setOnline] = useState(true)
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  const [error, setError] = useState("")
  const [success, setSuccess] = useState("")

  useEffect(() => {
    const authUser = storage.getAuth()
    if (!authUser?.business) {
      router.push("/login")
      return
    }
    setAuth(authUser)
    setBusinessName(authUser.business.businessName)
    setPhone(authUser.business.phone)
    setAddress(authUser.business.address)
    setOnline(authUser.business.online)
    setLoading(false)
  }, [router])

  const handleSave = async (e: React.FormEvent) => {
    e.preventDefault()
    setError("")
    setSuccess("")
    setSaving(true)

    try {
      if (!auth?.business) return

      storage.updateBusiness(auth.business.id, {
        businessName,
        phone,
        address,
        online,
      })

      const updated = storage.getBusinessById(auth.business.id)
      storage.setAuth({ ...auth, business: updated! })
      setAuth({ ...auth, business: updated! })

      setSuccess("Settings saved successfully!")
      setTimeout(() => setSuccess(""), 3000)
    } catch (err) {
      setError("Failed to save settings")
    } finally {
      setSaving(false)
    }
  }

  if (loading || !auth) return null

  return (
    <main className="min-h-screen bg-gradient-to-br from-background via-card to-background">
      <div className="max-w-2xl mx-auto p-4">
        <motion.div initial={{ opacity: 0, y: -20 }} animate={{ opacity: 1, y: 0 }}>
          <Link href="/dashboard" className="inline-block mb-8">
            <Button variant="ghost" size="sm">
              <ChevronLeft className="w-4 h-4 mr-2" />
              Back to Dashboard
            </Button>
          </Link>

          <div className="bg-card rounded-xl border border-border p-8 shadow-lg">
            <h1 className="text-3xl font-bold mb-2">Business Settings</h1>
            <p className="text-muted-foreground mb-8">Manage your business profile and preferences</p>

            <form onSubmit={handleSave} className="space-y-6">
              {error && (
                <motion.div
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  className="p-4 bg-destructive/10 border border-destructive rounded-lg text-destructive"
                >
                  {error}
                </motion.div>
              )}

              {success && (
                <motion.div
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  className="p-4 bg-green-500/10 border border-green-500 rounded-lg text-green-700 dark:text-green-400"
                >
                  {success}
                </motion.div>
              )}

              <div>
                <label className="block text-sm font-medium mb-2">Business Name</label>
                <input
                  type="text"
                  value={businessName}
                  onChange={(e) => setBusinessName(e.target.value)}
                  className="w-full bg-input border border-border rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-primary"
                  required
                />
              </div>

              <div>
                <label className="block text-sm font-medium mb-2">Phone Number</label>
                <input
                  type="tel"
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                  className="w-full bg-input border border-border rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-primary"
                  required
                />
              </div>

              <div>
                <label className="block text-sm font-medium mb-2">Address</label>
                <input
                  type="text"
                  value={address}
                  onChange={(e) => setAddress(e.target.value)}
                  className="w-full bg-input border border-border rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-primary"
                  required
                />
              </div>

              <div className="flex items-center gap-4">
                <label className="flex items-center gap-2 cursor-pointer">
                  <input
                    type="checkbox"
                    checked={online}
                    onChange={(e) => setOnline(e.target.checked)}
                    className="w-4 h-4"
                  />
                  <span className="text-sm font-medium">Online Status</span>
                </label>
                <span
                  className={`text-xs px-2 py-1 rounded-full ${
                    online ? "bg-green-500/20 text-green-700 dark:text-green-400" : "bg-muted text-muted-foreground"
                  }`}
                >
                  {online ? "Online" : "Offline"}
                </span>
              </div>

              <Button type="submit" disabled={saving} className="w-full">
                {saving ? "Saving..." : "Save Changes"}
              </Button>
            </form>
          </div>
        </motion.div>
      </div>
    </main>
  )
}
